import React, { Component } from 'react'

export class NewsItem extends Component {
  render() {
   let {title,description,imageUrl,url,author,publishedAt,name} = this.props;
    return (
                  <div className="card">
                  <img className="card-img-top"alt="" src={!imageUrl?'https://www.freeiconspng.com/uploads/no-image-icon-11.PNG': imageUrl}  style={{height: "250px"}}/>
                  <div className="card-body">
                    <h5 className="card-title">{title} <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                              New
                              <span class="visually-hidden">unread messages</span>
                            </span></h5>
                    <p className="card-text">{description}</p>
                    <a href={url} target="_blank" rel="noreferrer" className="btn btn-dark">Read More..</a>
                  </div>
                  <div className="card-footer text-muted">
                    By {!author?"Unknown" : author} {'&'} Publish at {publishedAt}
                  </div>
                </div>
    )
  }
}

export default NewsItem
