import React, { Component } from 'react'
import NewsItem from './NewsItem'
import PropTypes from 'prop-types'


export class News extends Component {

  static defaultProps = {
    country : 'in',
    category : 'general'
  }

  static propTypes = {
    country : PropTypes.string,
    category : PropTypes.string,
  }

  constructor(){
    super();
    console.log("Hi");
    this.state = {
      articles : [],
      loading : false,
      page : 1
    }
  }

 async componentDidMount(){
    
    let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=be495c7f720542b89250c7bd204e7526&page=1&pageSize=18`;
    let data = await fetch(url);
    let parseData = await data.json();
    console.log(parseData);
    this.setState({articles : parseData.articles , totalResults : parseData.totalResults})

  }

  handleNextclick = async ()=>{

    if(this.state.page + 1 > Math.ceil(this.state.totalResults/20))
    {
      alert("This Is Last Page");
    }
    else
    {
    let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=be495c7f720542b89250c7bd204e7526&page= ${this.state.page + 1}&pageSize=18`;
    let data = await fetch(url);
    let parseData = await data.json();
    console.log(parseData);
    this.setState({
      page : this.state.page + 1,
      articles : parseData.articles
    })
  }
   
  }

  handlePrevclick = async ()=>{

    let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=be495c7f720542b89250c7bd204e7526&page= ${this.state.page - 1}&pageSize=18`;
    let data = await fetch(url);
    let parseData = await data.json();
    console.log(parseData);
    this.setState({
      page : this.state.page - 1,
      articles : parseData.articles
    })

  }

  render() {
    return (
      <div className="container my-3">
        <h2 className="text-center">NewsMonkey - Top HeadLines</h2>
        
        <div className="row">
            {this.state.articles.map((element)=>{
              return <div className="col-md-4 my-3" key={element.url}>
              <NewsItem  imageUrl={element.urlToImage} title={element.title?element.title:""} description={element.description} url={element.url} author={element.author}
               publishedAt={element.publishedAt}></NewsItem>
            </div>
            })}
        </div>
        
            <div className="container">
                <div className="d-grid gap-2 col-6 mx-auto">
                    <button className="btn btn-dark" type="button" onClick={this.handleNextclick} >Next Page &rarr;</button>
                    <button disabled={this.state.page <= 1} className="btn btn-dark" type="button" onClick={this.handlePrevclick} >&larr; Previous Page</button>
              </div>
            </div>

      </div>
    )
  }
}

export default News
